import React, { Component } from 'react'

export default class More extends Component {


    constructor(){
        super();
        this.state={
            content :'',
        }
    }

    componentDidMount(){
        let content = this.props.location.state.content;
        this.setState({
            content:content
        })
    }

    render() {
        return (
            <div>
                <div dangerouslySetInnerHTML={{__html:this.state.content}}></div>
            </div>
        )
    }
}
