import React, { Component } from 'react'
import Axios from 'axios';
import Sider from "./Sider2";
import {NavLink} from "react-router-dom";

export default class Request extends Component {
    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    componentDidMount(){
        if(this.props.location.state==undefined){
            this.url='/home/all';
        }else{
            this.url= this.props.location.state.url;
        }
        console.log(this.props);
        if(this.props.match.params.id!==1){
            fetch('https://cnodejs.org/api/v1/topics?tab=all&page='+this.props.match.params.id).then((res)=>res.json())
            .then((res)=>{
                console.log(res);
                this.setState({
                    data: res.data
                })
            })
        }else{

        
        fetch('https://cnodejs.org/api/v1/topics?tab=all&page=1').then((res)=>res.json())
            .then((res)=>{
                console.log(res);
                this.setState({
                    data: res.data
                })
            })
        }
    }

    componentDidUpdate(prevProps){
        let id = this.props.match.params.id;
        console.log(this.props.match);
        console.log(123456564898789889);
        console.log(this.props);
        console.log(id);
        console.log("old:"+prevProps.match.params.id);
        if(id !== prevProps.match.params.id){
            fetch('https://cnodejs.org/api/v1/topics?tab=all&page='+id)
            .then((res)=>res.json())
            .then((res)=>{
                console.log(res);
                this.setState({
                    data: res.data
                },()=>{
                    console.log(999999999999999999999);
                    console.log(this.state.data);
                })
            })
        }else{
            console.log("all failed");
        }
    }
    

    render() {
        // console.log(this.state.data);
        // let {url}=this.props.match;
        // console.log(this.props.match);
        return (
            <div>
                <ul>
                    {
                        this.state.data.map(
                            (item,index)=>(
                                <li key={index} style={{clear:"both"}}>
                                <img src={item.author.avatar_url} style={{width:'20px',height:'20px',float:'left'}}></img>
                                <p style={{float:'left'}}>{item.reply_count}</p>
                                <p style={{float:'left'}}>/</p>
                                <p style={{float:'left'}}>{item.visit_count}</p>
                                <p style={{float:'left',color:'green',fontSize:'10px'}}>置顶</p>
                                <NavLink to={{pathname:this.url+'/topic/'+item.id,state:{content:item.content}}}><span style={{marginLeft:'5px'}}>{item.title}</span></NavLink>
                                {/* <a style={{float:'left'}}>{item.title}</a> */}
                                <p style={{fontSize:'10px',float:"right"}}>两天前</p>
                                <img src={item.author.avatar_url} style={{width:'20px',height:'20px',float:'right'}}></img>
                            </li>
                            )
                        )
                    }
                    
                </ul>
                <Sider url={this.url}/>
            </div>
        )
    }
}