import React, { Component } from 'react';
import {NavLink} from "react-router-dom";

export default class Login extends Component {
    render() {
        return (
            <div style={{border:'solid red 2px'}}>
                <li style={{marginLeft:'60px',marginTop:'40px'}}><span>用户名</span><input></input></li>
                <li style={{marginLeft:'60px',marginTop:'40px'}}><span>密码</span><input></input></li>
                <NavLink to={`/home/all/1`}><li style={{marginLeft:'60px',marginTop:'40px'}}><button style={{backgroundColor:'blue',width:'200px'}}>登陆</button></li></NavLink>
            </div>
        )
    }
}
