import React, { Component } from 'react'
import Axios from 'axios';
export default class Request extends Component {
    constructor(){
        super();
        this.state = {
            data:[]
        }
    }
    componentDidMount(){
        fetch('https://cnodejs.org/api/v1/topics?tab=all&page=1').then((res)=>res.json())
            .then((res)=>{
                console.log(res);
                this.setState({
                    data: res.data
                })
            })
    }
    render() {
        console.log(this.state.data);
        return (
            <div>
                <h1>请求接口</h1>
                <ul>
                    {
                        this.state.data.map(
                            (item,index)=>(
                               <li key={index}>
                                    <h2>{item.title}</h2>
                                </li>
                            )
                        )
                    }
                    
                </ul>
            </div>
        )
    }
}