import React, { Component } from 'react'
import {Link,Route} from 'react-router-dom';
import Good from './Good';
import All from './All';
import Share from './Share';
import Job from './Job';
import Answer from './Answer';
import Text from './Text';
import Request from '../Request';
import Topic from '../Topic';
import NoMatch from '../NoMatch';
import More from './More';
// import {HashRouter as Router,Route,Switch,Redirect,withRouter} from 'react-router-dom';
export default class Home extends Component {
    render() {
        let url = this.props.match.url///home
        return (
            <div>

                <div style={{height:'40px'}}>
                    <Link to={{pathname:url+'/all/1',state:{url:url+'/all'}}} style={{marginLeft:'10px'}}>全部</Link>
                    <Link to={{pathname:url+'/good/1',state:{url:url+'/good'}}} style={{marginLeft:'10px'}}>精华</Link>
                    <Link to={{pathname:url+'/share/1',state:{url:url+'/share'}}} style={{marginLeft:'10px'}}>分享</Link>
                    <Link to={{pathname:url+'/job',state:{url:url+'/job'}}} style={{marginLeft:'10px'}}>招聘</Link>
                    <Link to={{pathname:url+'/answer',state:{url:url+'/answer'}}} style={{marginLeft:'10px'}}>回答</Link>
                    <Link to={{pathname:url+'/text',state:{url:url+'/text'}}} style={{marginLeft:'10px'}}>客户端测试</Link>

                    {/* {
                    [1,2,3,4,5,6].map((item)=>(
                        <li key={item}><Link to={url+'/topics?tab=all/'+item}>{item}</Link></li>   
                    ))
                } */}
                </div>
                <div>
                    {/* <Switch> */}
                    <Route exact path={`${url}/all`} component={All} />
                    <Route exact path={`${url}/all/:id`} component={All} />
                    <Route exact path={`${url}/all/topic/:index`} component={More} />
                    {/* <Route path={`${url}/all/:id`} component={Topic} /> */}
                    <Route exact path={`${url}/good`} component={Good} />
                    <Route exact path={`${url}/good/:id`} component={Good} />
                    <Route exact path={`${url}/good/topic/:index`} component={More} />
                    <Route exact path={`${url}/share`} component={Share} />
                    <Route exact path={`${url}/share/:id`} component={Share} />
                    <Route exact path={`${url}/share/topic/:index`} component={More} />
                    <Route exact path={`${url}/job`} component={Job} />
                    <Route exact path={`${url}/job/:id`} component={Job} />
                    <Route exact path={`${url}/job/topic/:index`} component={More} />
                    <Route exact path={`${url}/answer`} component={Answer} />
                    <Route exact path={`${url}/answer/:id`} component={Answer} />
                    <Route exact path={`${url}/answer/topic/:index`} component={More} />
                    <Route exact path={`${url}/text`} component={Text} />
                    <Route exact path={`${url}/text/:id`} component={Text} />
                    <Route exact path={`${url}/text/topic/:index`} component={More} />
                    {/* <Route path={`${url}/topics?tab=all/:page`} component={Topic}/> */}
                    {/* <Route path={`${url}/nomatch`} component={NoMatch} />
                    <Redirect to={`${url}/nomatch`}/> */}
                    {/* </Switch> */}
                </div>
            </div>
        )
    }
} 